}
.sv-previewer-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
  background: rgba(0, 0, 0, 0.9);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.sv-previewer-wrapper .sv-previewer-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: nowrap;
        flex-flow: nowrap;
    height: 100%;
    -webkit-animation: fade-in-out .5s linear;
            animation: fade-in-out .5s linear;
}
@-webkit-keyframes fade-in-out {
from {
    -webkit-transform: translate(0, -200px);
            transform: translate(0, -200px);
    opacity: 0;
}
to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
}
}
@keyframes fade-in-out {
from {
    -webkit-transform: translate(0, -200px);
            transform: translate(0, -200px);
    opacity: 0;
}
to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
}
}
.sv-previewer-wrapper .sv-previewer-container .sv-previewer-section {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      overflow: hidden;
}
.sv-previewer-wrapper .sv-previewer-indicator {
    position: absolute;
    bottom: 5rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    color: #fff;
    font-size: 2rem;
}



