/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-button {
  padding: 0.8rem 2rem;
  border: 1px solid #dddee1;
  border-radius: 0.4rem;
  outline: none;
  text-align: center;
  font-size: 1.4rem;
  color: #495060;
}
.sv-button.sv-button-light {
    background: #fff;
}
.sv-button.sv-button-light:active, .sv-button.sv-button-light .is-highlight {
      background: #f7f7f7;
}
.sv-button.sv-button-stable {
    background: #f7f7f7;
    border-color: #dddee1;
}
.sv-button.sv-button-stable:active, .sv-button.sv-button-stable.is-highlight {
      background: #d7d7d7;
}
.sv-button.sv-button-primary {
    background: #2d8cf0;
    border-color: #2d8cf0;
    color: #fff;
}
.sv-button.sv-button-primary:active, .sv-button.sv-button-primary.is-highlight {
      background: #0062cc;
      border-color: #0062cc;
}
.sv-button.sv-button-primary.sv-button-outline {
      color: #2d8cf0;
      background: #fff;
}
.sv-button.sv-button-info {
    background: #50bfff;
    color: #fff;
}
.sv-button.sv-button-info:active, .sv-button.sv-button-info.is-highlight {
      background: #1D8CE0;
}
.sv-button.sv-button-info.sv-button-outline {
      color: #20A0FF;
      background: #fff;
}
.sv-button.sv-button-success {
    background-color: #65ccaf;
    border-color: #65ccaf;
    color: #fff;
}
.sv-button.sv-button-success:active, .sv-button.sv-button-success.is-highlight {
      background: #19be6b;
}
.sv-button.sv-button-success.sv-button-outline {
      color: #65ccaf;
      background: #fff;
}
.sv-button.sv-button-warning {
    background: #f7ba2a;
    border-color: #f7ba2a;
    color: #fff;
}
.sv-button.sv-button-warning:active, .sv-button.sv-button-warning.is-highlight {
      background: #dea726;
}
.sv-button.sv-button-warning.sv-button-outline {
      color: #f7ba2a;
      background: #fff;
}
.sv-button.sv-button-lively {
    background: #ff7800;
    border-color: #ff7800;
    color: #fff;
}
.sv-button.sv-button-lively:active, .sv-button.sv-button-lively.is-highlight {
      background: #e56c00;
}
.sv-button.sv-button-lively.sv-button-outline {
      color: #ff7800;
      background: #fff;
}
.sv-button.sv-button-danger {
    background: #ff4949;
    color: #fff;
}
.sv-button.sv-button-danger:active, .sv-button.sv-button-danger.is-highlight {
      background: #e64242;
}
.sv-button.sv-button-danger.sv-button-disabled {
      background: #ff4949;
}
.sv-button.sv-button-danger.sv-button-outline {
      color: #ff4949;
      background: #fff;
}
.sv-button.sv-button-royal {
    background: #8a6de9;
    border-color: #8a6de9;
    color: #fff;
}
.sv-button.sv-button-royal:active, .sv-button.sv-button-royal.is-highlight {
      background: #6641e2;
}
.sv-button.sv-button-royal.sv-button-outline {
      color: #8a6de9;
      background: #fff;
}
.sv-button.sv-button-text {
    background: transparent;
    border: none;
}
.sv-button.sv-button-text:active {
      background: transparent;
}
.sv-button.sv-button-text.sv-button-stable {
      color: #495060;
}
.sv-button.sv-button-text.sv-button-stable:active, .sv-button.sv-button-text.sv-button-stable.is-highlight {
        color: #d7d7d7;
}
.sv-button.sv-button-text.sv-button-primary {
      color: #2d8cf0;
}
.sv-button.sv-button-text.sv-button-primary:active, .sv-button.sv-button-text.sv-button-primary.is-highlight {
        color: #0062cc;
}
.sv-button.sv-button-text.sv-button-danger {
      color: #ff4949;
}
.sv-button.sv-button-text.sv-button-danger:active, .sv-button.sv-button-text.sv-button-danger.is-highlight {
        color: #e64242;
}
.sv-button.sv-button-text.sv-button-success {
      color: #65ccaf;
}
.sv-button.sv-button-small {
    padding: 0.5rem 1.5rem;
}
.sv-button.sv-button-large {
    padding: 1rem 4rem;
}
.sv-button.sv-button-full {
    width: 100%;
    height: 100%;
    min-height: 34px;
    font-size: 1.6rem;
}
.sv-button.sv-button-disabled {
    opacity: .5;
}
.sv-button.sv-button-line {
    border-radius: 0;
}
.sv-button.sv-button-circle {
    border-radius: 50%;
}



