/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-picker-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 21rem;
  overflow: hidden;
}
.sv-picker-group .sv-picker-list {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.sv-picker-group .sv-picker-list .sv-picker-item {
      height: 3rem;
      line-height: 3rem;
      text-align: center;
      color: #999;
}
.sv-picker-group .sv-picker-list .sv-picker-item.active {
        color: #666;
}



