.sv-palette-button {
  position: relative;
}
.sv-palette-button .sv-palette-button-main {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 6rem;
    height: 6rem;
    border: none;
    outline: none;
    border-radius: 3rem;
    background: #ff4949;
    color: #fff;
    font-size: 1.9rem;
}
.sv-palette-button .sv-palette-button-main.expanding {
      -webkit-animation: shake .2s ease-in;
              animation: shake .2s ease-in;
}
@-webkit-keyframes shake {
0% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
50% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes shake {
0% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
50% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
.sv-palette-button .sv-palette-button-child {
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    z-index: -1;
}
.sv-palette-button .sv-palette-button-child.expanding {
      top: -1rem;
      left: -100%;
      -webkit-transform: rotate(720deg);
              transform: rotate(720deg);
      transition: all .5s ease-in;
}
.sv-palette-button .sv-palette-button-child.collapsing {
      top: 1.5rem;
      left: 1.5rem;
      -webkit-transform: rotate(0);
              transform: rotate(0);
      transition: all .5s ease-in;
}
.sv-palette-button .sv-palette-button-child .sv-button {
      width: 3rem;
      height: 3rem;
      border: none;
      border-radius: 1.5rem;
      background: #ff4949;
      color: #fff;
      font-size: 1.3rem;
}
.sv-palette-button .sv-palette-button-child .sv-button.sv-button-normal {
        padding: 0;
}



