/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-radio {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  white-space: nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 0;
  line-height: 1;
}
.sv-radio .sv-radio-icon {
    position: relative;
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 0.8rem;
    border: 0.1rem solid #65ccaf;
}
.sv-radio .sv-radio-icon.active::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      width: 0.9rem;
      height: 0.9rem;
      border-radius: 0.45rem;
      background: #65ccaf;
}
.sv-radio .sv-radio-label {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin-left: 0.2rem;
    font-size: 1.4rem;
    color: #333;
}



