@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?jyji52');
  src:  url('fonts/icomoon.eot?jyji52#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?jyji52') format('truetype'),
    url('fonts/icomoon.woff?jyji52') format('woff'),
    url('fonts/icomoon.svg?jyji52#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-no-data:before {
  content: "\e900";
  color: #cfcfcf;
}



