
@charset "UTF-8";
/*********************** reset ***********************/
@import url(style.css);
html, body, div, span, h1, h2, h3, h4, h5, h6, p, img, ul, li, figure, figcaption {
  margin: 0;
  padding: 0;
}
html {
  /*
  移动端如何定义字体font-family
@ --------------------------------------中文字体的英文名称
@ 宋体      SimSun
@ 黑体      SimHei
@ 微信雅黑   Microsoft Yahei
@ 微软正黑体 Microsoft JhengHei
@ 新宋体    NSimSun
@ 新细明体  MingLiU
@ 细明体    MingLiU
@ 标楷体    DFKai-SB
@ 仿宋     FangSong
@ 楷体     KaiTi
@ 仿宋_GB2312  FangSong_GB2312
@ 楷体_GB2312  KaiTi_GB2312
@
@ 说明：中文字体多数使用宋体、雅黑，英文用Helvetica
   */
  font-family: 'Microsoft YaHei', 'SimSun', 'Tahoma', 'Helvetica';
  font-size: 10px;
  background: #fff;
  height: 100%;
  min-height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}
img {
  border: 0;
  vertical-align: middle;
}
ol, ul {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover, a:focus {
    outline: none;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: 100%;
}
button, input, textarea {
  margin: 0;
  font: inherit;
  color: inherit;
  outline: none;
}
* {
  box-sizing: border-box;
}
:before, :after {
  box-sizing: border-box;
}
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/*********************** sass function ***********************/
/* px to rem  */
/*********************** driver mobile UI style ***********************/
[v-cloak] {
  display: none !important;
}
body {
  width: 100%;
  height: 100%;
  min-height: 100%;
}

/*基本颜色*/
/*白色*/
/*灰色*/
/*蓝色*/
/*深蓝色*/
/*浅蓝色*/
/*绿色*/
/*黄色*/
/*红色*/
/*深红色*/
/*橙色*/
/*紫色*/
/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-rating-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.sv-rating-wrapper.rating-text-bottom {
    display: block;
}
.sv-rating-wrapper.rating-text-bottom .sv-rating-stars {
      display: block;
      margin-bottom: 1rem;
}
.sv-rating-wrapper.rating-text-bottom .sv-rating-text {
      display: block;
}
.sv-rating-wrapper .sv-rating-stars {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.sv-rating-wrapper .sv-rating-stars .rating-star {
      margin-right: 1rem;
      font-size: 3rem;
      color: #e4e4e4;
      transition: color .5s;
}
.sv-rating-wrapper .sv-rating-stars .rating-star:last-child {
        margin-right: 0;
}
.sv-rating-wrapper .sv-rating-stars .rating-star.rating-star-active {
        color: #ff4949;
}
.sv-rating-wrapper .sv-rating-text {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    margin-left: 1rem;
    font-size: 1.4rem;
    color: #ff4949;
}



