.download-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  background: url("../../assets/download/driver/bg.png") no-repeat;
  background-size: 100% 100%;
}
.download-wrapper .hgroup {
    padding: 1.9rem 0rem 0rem 5.1rem;
    color: #fff;
    font-size: 3rem;
}
.download-wrapper .hgroup .title {
      margin-bottom: 2rem;
      font-weight: bold;
}
.download-wrapper .footer {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
}
.download-wrapper .footer .button-group .button-download {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      width: 20rem;
      height: 4.4rem;
      border: none;
      outline: none;
      border-radius: 0.5rem;
      color: #fff;
      font-size: 1.5rem;
      background: linear-gradient(to right, #f79415, #f17f17);
      white-space: nowrap;
      cursor: pointer;
}
.download-wrapper .footer .button-group .button-download .icon {
        width: 1.5rem;
        height: 2rem;
        margin-right: 0.8rem;
}
.download-wrapper .footer .button-group .button-download:first-child {
        margin-bottom: 3rem;
}
.download-wrapper .footer .button-next {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      margin-top: 1rem;
      text-align: center;
}
.download-wrapper .footer .button-next .image {
        width: 2rem;
        height: 1.3rem;
        -webkit-animation: loop 1.5s infinite;
                animation: loop 1.5s infinite;
}
@-webkit-keyframes loop {
from {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
}
to {
    -webkit-transform: translateY(10px) scale(1.2);
            transform: translateY(10px) scale(1.2);
}
}
@keyframes loop {
from {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
}
to {
    -webkit-transform: translateY(10px) scale(1.2);
            transform: translateY(10px) scale(1.2);
}
}
.download-carousel-wrapper {
  width: 100%;
  height: 100%;
  background: #f2f2f2;
  position: relative;
}
.download-carousel-wrapper .phone-box-image-wrapper {
    position: absolute;
    top: 8rem;
    left: 50%;
    z-index: 1;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 25rem;
    height: 46rem;
}
.download-carousel-wrapper .phone-box-image-wrapper .phone-box-image {
      width: 100%;
      height: 100%;
}
.download-carousel-wrapper .title-wrapper {
    padding-top: 3rem;
    font-size: 2.4rem;
    font-weight: bold;
    color: #333;
    text-align: center;
    overflow: hidden;
}
.download-carousel-wrapper .title-wrapper .title-list {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
.download-carousel-wrapper .title-wrapper .title-list .title-item {
        width: 100%;
        -ms-flex-negative: 0;
            flex-shrink: 0;
        text-align: center;
}
.download-carousel-wrapper .carousel {
    position: absolute;
    top: 14.3rem;
    left: 50%;
    z-index: 2;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 21.7rem;
    height: 39.7rem;
}
.download-carousel-wrapper .carousel .image {
      width: 100%;
      height: 100%;
}
.download-carousel-wrapper .sv-carousel-control {
    position: absolute;
    top: 50%;
    z-index: 999;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 3rem;
    height: 3rem;
    border: 1px solid #989898;
    border-radius: 50%;
    outline: none;
    background: transparent;
}
.download-carousel-wrapper .sv-carousel-control.disabled {
      border-color: #cdcdcd;
}
.download-carousel-wrapper .sv-carousel-control.disabled .arrow-icon {
        border-left-color: #ccc;
        border-bottom-color: #ccc;
}
.download-carousel-wrapper .sv-carousel-control .arrow-icon {
      display: inline-block;
      width: 1rem;
      height: 1rem;
      border-left: 2px solid #999;
      border-bottom: 2px solid #999;
}
.download-carousel-wrapper .sv-carousel-control.sv-carousel-control-prev {
      left: 1rem;
}
.download-carousel-wrapper .sv-carousel-control.sv-carousel-control-prev .arrow-icon {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
.download-carousel-wrapper .sv-carousel-control.sv-carousel-control-next {
      right: 1rem;
}
.download-carousel-wrapper .sv-carousel-control.sv-carousel-control-next .arrow-icon {
        -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
}
.download-carousel-wrapper .footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 3.5rem;
    line-height: 3.5rem;
    text-align: center;
    background: #fff;
    color: #999;
    font-size: 1.5rem;
}



