.sv-dialog {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 80%;
  border-radius: 1rem;
  background: #fff;
  overflow: hidden;
  /*animation: zoom-out-in .2s;*/
}
@-webkit-keyframes zoom-out-in {
0% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    -webkit-transform-origin: center;
            transform-origin: center;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform-origin: center;
            transform-origin: center;
}
}
@keyframes zoom-out-in {
0% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    -webkit-transform-origin: center;
            transform-origin: center;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform-origin: center;
            transform-origin: center;
}
}
.sv-dialog .sv-dialog-header {
    padding: 1.5rem 1.5rem 0rem 1.5rem;
    text-align: center;
    font-size: 1.6rem;
    color: #333;
}
.sv-dialog .sv-dialog-body {
    padding: 1.5rem;
    text-align: center;
    font-size: 1.3rem;
    color: #333;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt {
      text-align: left;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-top, .sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-left {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-top .input-label, .sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-left .input-label {
          display: block;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-left {
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-left .input-label {
          margin-right: 0.3rem;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-left .sv-input-wrapper {
          width: auto;
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-top {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper.label-top .input-label {
          margin-bottom: 0.3rem;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper .input-label {
        display: none;
        font-size: 0.6rem;
        color: #666;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-input-wrapper .sv-input-wrapper.invalid {
        border: 1px solid #ff4949;
        transition: border .3s ease-in;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-validate-message {
        margin-top: 0.2rem;
        min-height: 1.2rem;
        line-height: 1;
        text-align: left;
        visibility: hidden;
        opacity: 0;
        color: #ff4949;
        font-size: 0.5rem;
}
.sv-dialog .sv-dialog-body .sv-dialog-body-prompt .prompt-validate-message.visible {
          visibility: visible;
          opacity: 1;
          transition: opacity .3s ease-in;
}
.sv-dialog .sv-dialog-footer {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 4rem;
    border-top: 1px solid #ddd;
}
.sv-dialog .sv-dialog-footer .button {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      border: none;
      outline: none;
      border-right: 1px solid #ddd;
      letter-spacing: 0.2rem;
      font-size: 1.6rem;
      color: #333;
      background: #fff;
}
.sv-dialog .sv-dialog-footer .button:last-child {
        border-right: none;
}
.sv-dialog .sv-dialog-footer .button:active {
        background: #f2f2f2;
}
.sv-dialog .sv-dialog-footer .button.button-cancel {
        color: #999;
}
.sv-dialog .sv-dialog-footer .button.button-confirm {
        color: #2d8cf0;
}
.sv-dialog .sv-dialog-footer .button.button-confirm.button-disabled {
          opacity: .6;
}



