
@charset "UTF-8";
/*********************** reset ***********************/
html, body, div, span, h1, h2, h3, h4, h5, h6, p, img, ul, li, figure, figcaption {
  margin: 0;
  padding: 0;
}
html {
  /*
  移动端如何定义字体font-family
@ --------------------------------------中文字体的英文名称
@ 宋体      SimSun
@ 黑体      SimHei
@ 微信雅黑   Microsoft Yahei
@ 微软正黑体 Microsoft JhengHei
@ 新宋体    NSimSun
@ 新细明体  MingLiU
@ 细明体    MingLiU
@ 标楷体    DFKai-SB
@ 仿宋     FangSong
@ 楷体     KaiTi
@ 仿宋_GB2312  FangSong_GB2312
@ 楷体_GB2312  KaiTi_GB2312
@
@ 说明：中文字体多数使用宋体、雅黑，英文用Helvetica
   */
  font-family: 'Microsoft YaHei', 'SimSun', 'Tahoma', 'Helvetica';
  font-size: 10px;
  background: #fff;
  height: 100%;
  min-height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}
img {
  border: 0;
  vertical-align: middle;
}
ol, ul {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover, a:focus {
    outline: none;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: 100%;
}
button, input, textarea {
  margin: 0;
  font: inherit;
  color: inherit;
  outline: none;
}
* {
  box-sizing: border-box;
}
:before, :after {
  box-sizing: border-box;
}
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/*********************** sass function ***********************/
/* px to rem  */
/*********************** driver mobile UI style ***********************/
[v-cloak] {
  display: none !important;
}
body {
  width: 100%;
  height: 100%;
  min-height: 100%;
}

/*基本颜色*/
/*白色*/
/*灰色*/
/*蓝色*/
/*深蓝色*/
/*浅蓝色*/
/*绿色*/
/*黄色*/
/*红色*/
/*深红色*/
/*橙色*/
/*紫色*/
/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-loadmore {
  min-height: 100%;
}
.sv-loadmore .sv-loadmore-content.is-dropped {
    transition: .2s;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-bottom-allloaded-text {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    padding: 0.5rem 0rem;
    font-size: 1.4rem;
    text-align: center;
    color: #999;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-bottom-allloaded-text:before, .sv-loadmore .sv-loadmore-content .sv-loadmore-bottom-allloaded-text:after {
      content: '';
      display: inline-block;
      width: 5rem;
      height: 0;
      border: 1px solid #999;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-bottom-allloaded-text:before {
      margin-right: 0.5rem;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-bottom-allloaded-text:after {
      margin-left: 0.5rem;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-top, .sv-loadmore .sv-loadmore-content .sv-loadmore-bottom {
    color: #999;
    font-size: 1.6rem;
    text-align: center;
    height: 2.5rem;
    line-height: 2.5rem;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-top {
    margin-top: -2.5rem;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-bottom {
    margin-bottom: -2.5rem;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-spinner {
    display: inline-block;
    margin-right: 0.25rem;
    vertical-align: middle;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-spinner.rotate {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-spinner .loading3 {
      border-radius: 50%;
      height: 1.5rem;
      position: relative;
      width: 1.5rem;
      /* css3 keyframes - animation 3 */
}
@-webkit-keyframes anim3 {
from {
    -webkit-transform: rotateY(0deg);
}
50% {
    -webkit-transform: rotateY(180deg);
}
to {
    -webkit-transform: rotateY(360deg);
}
}
@keyframes anim3 {
from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-spinner .loading3 div:nth-child(1), .sv-loadmore .sv-loadmore-content .sv-loadmore-spinner .loading3 div:nth-child(2) {
        background-color: rgba(255, 255, 255, 0.2);
        border: 0.25rem solid #FFF;
        border-radius: 50%;
        box-shadow: 0 0 0.25rem 0 #ff4949;
        height: 1.5rem;
        position: absolute;
        top: 0;
        /* set top and bottom border widths to 0 */
        border-top-width: 0;
        border-bottom-width: 0;
        -webkit-animation: anim3 2s linear infinite;
                animation: anim3 2s linear infinite;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-spinner .loading3 div:nth-child(1) {
        border-color: #ff4949;
        left: 0;
        width: 1rem;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-spinner .loading3 div:nth-child(2) {
        border-color: #ff4949;
        left: -0.25rem;
        width: 1.5rem;
        -webkit-animation-delay: 0.5s;
                animation-delay: 0.5s;
}
.sv-loadmore .sv-loadmore-content .sv-loadmore-text {
    vertical-align: middle;
}



