/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-input-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 4rem;
  overflow: hidden;
  border: 1px solid #ddd;
  border-radius: 0.2rem;
  font-size: 1.3rem;
}
.sv-input-wrapper.sv-item-input {
    border: none;
    border-bottom: 1px solid #ddd;
}
.sv-input-wrapper.sv-item-input .sv-input-left .sv-input-label {
      font-size: 1.5rem;
      color: #333;
}
.sv-input-wrapper .sv-input-icon {
    padding: 0rem 0.7rem;
    height: 3rem;
    line-height: 3rem;
    font-size: 2rem;
    text-align: center;
    color: #bbb;
}
.sv-input-wrapper .sv-input-icon.left-icon {
      /*border-right: 1px solid #ddd;*/
}
.sv-input-wrapper .sv-input-icon.right-icon {
      /*border-left: 1px solid #ddd;*/
}
.sv-input-wrapper .sv-input {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding: 0rem 1rem;
    color: #666;
    border: none;
    width: 100%;
    height: 100%;
}
.sv-input-wrapper .sv-input.disabled {
      background: #f5f7fa;
      color: #c0c4cc;
}
.sv-input-wrapper .sv-input.disabled::-webkit-input-placeholder {
        color: #c0c4cc;
}
.sv-input-wrapper .sv-input-left, .sv-input-wrapper .sv-input-right {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100%;
}
.sv-input-wrapper .sv-input-right .sv-input-unit {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100%;
    padding: 0rem 0.7rem;
    box-sizing: border-box;
    border-left: 1px solid #bfcbd9;
    background-color: #fbfdff;
    color: #97a8be;
    font-style: normal;
    font-size: 0.7rem;
}
.sv-input-wrapper .sv-input-right .sv-input-unit .unit-text {
      max-width: 5rem;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
}



