/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-badge {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.35rem 0.6rem;
  line-height: 1;
  color: #fff;
  font-style: normal;
  font-size: 1.3rem;
}
.sv-badge.is-circle {
    padding-top: 0;
    padding-bottom: 0;
    border-radius: 50%;
}
.sv-badge.sv-badge-fill.sv-badge-light {
    background: #fff;
    color: #333;
}
.sv-badge.sv-badge-fill.sv-badge-stable {
    background: #e8e8e8;
    color: #333;
}
.sv-badge.sv-badge-fill.sv-badge-success {
    background: #65ccaf;
}
.sv-badge.sv-badge-fill.sv-badge-warning {
    background: #f7ba2a;
}
.sv-badge.sv-badge-fill.sv-badge-info {
    background: #50bfff;
}
.sv-badge.sv-badge-fill.sv-badge-danger {
    background: #ff4949;
}
.sv-badge.sv-badge-fill.sv-badge-primary {
    background: #2d8cf0;
}
.sv-badge.sv-badge-fill.sv-badge-lively {
    background: #ff7800;
}
.sv-badge.sv-badge-fill.sv-badge-royal {
    background: #8a6de9;
}
.sv-badge.sv-badge-outline {
    border-width: 1px;
    border-style: solid;
    background: #fff;
}
.sv-badge.sv-badge-outline.sv-badge-light {
      border-color: #fff;
      color: #333;
}
.sv-badge.sv-badge-outline.sv-badge-stable {
      border-color: #e8e8e8;
      color: #e8e8e8;
}
.sv-badge.sv-badge-outline.sv-badge-success {
      border-color: #65ccaf;
      color: #65ccaf;
}
.sv-badge.sv-badge-outline.sv-badge-warning {
      border-color: #f7ba2a;
      color: #f7ba2a;
}
.sv-badge.sv-badge-outline.sv-badge-info {
      border-color: #50bfff;
      color: #50bfff;
}
.sv-badge.sv-badge-outline.sv-badge-danger {
      border-color: #ff4949;
      color: #ff4949;
}
.sv-badge.sv-badge-outline.sv-badge-primary {
      border-color: #2d8cf0;
      color: #2d8cf0;
}
.sv-badge.sv-badge-outline.sv-badge-lively {
      border-color: #ff7800;
      color: #2d8cf0;
}
.sv-badge.sv-badge-outline.sv-badge-royal {
      border-color: #8a6de9;
      color: #8a6de9;
}
.sv-badge.sv-badge-small {
    padding: 0.3rem 0.4rem;
}
.sv-badge.sv-badge-large {
    padding: 0.5rem 0.7rem;
}



