}
.sv-index-list-wrapper::-webkit-scrollbar {
  display: none;
  width: 0;
}
.sv-index-list-wrapper .sv-index-mark {
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
  padding-left: 1.5rem;
  border-bottom: 1px solid #ccc;
  background: #fff;
  color: #65ccaf;
  line-height: 1.5rem;
}
.sv-index-list-wrapper .sv-index-list.hidden {
  display: none;
}
.sv-index-list-wrapper .sv-index-handle {
  position: fixed;
  top: 50%;
  right: 0.5rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 1rem;
  color: #333;
}
.sv-index-list-wrapper .sv-index-handle .sv-index-handle-letter {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0.5rem 0rem;
    line-height: 1;
}
.sv-index-list-wrapper .sv-index-handle .sv-index-handle-letter.active {
      width: 1.5rem;
      height: 1.5rem;
      border-radius: 50%;
      background: #65ccaf;
      color: #fff;
}
.sv-index-list-wrapper .sv-index-indicator {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 5rem;
  height: 5rem;
  border-radius: 1rem;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  font-size: 2rem;
  font-style: normal;
  opacity: 0;
  transition: opacity .8s ease-out;
}
.sv-index-list-wrapper .sv-index-indicator.active {
    opacity: 1;
    transition: opacity 0s;
}



