@font-face {
  font-family: 'sv-icomoon';
  src:  url('fonts/sv-icomoon.eot?w6cq9w');
  src:  url('fonts/sv-icomoon.eot?w6cq9w#iefix') format('embedded-opentype'),
    url('fonts/sv-icomoon.ttf?w6cq9w') format('truetype'),
    url('fonts/sv-icomoon.woff?w6cq9w') format('woff'),
    url('fonts/sv-icomoon.svg?w6cq9w#sv-icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="sv-icon-"], [class*=" sv-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sv-icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

