.sv-carousel {
  position: relative;
  overflow: hidden;
}
.sv-carousel .sv-carousel-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    height: 100%;
}
.sv-carousel .sv-carousel-list .sv-carousel-item {
      width: 100%;
      height: 100%;
      -ms-flex-negative: 0;
          flex-shrink: 0;
}
.sv-carousel .sv-carousel-list .sv-carousel-item .image {
        width: 100%;
        height: 100%;
}
.sv-carousel .sv-carousel-indicator {
    position: absolute;
    bottom: 1rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.sv-carousel .sv-carousel-indicator .indicator-item {
      width: 1rem;
      height: 0;
      border-top: 0.2rem solid #e8e8e8;
}
.sv-carousel .sv-carousel-indicator .indicator-item:not(:last-child) {
        margin-right: 0.5rem;
}
.sv-carousel .sv-carousel-indicator .indicator-item.active {
        width: 0.5rem;
        height: 0.5rem;
        border-radius: 50%;
        background: transparent;
        border: 0.1rem solid #ff7800;
}
.sv-carousel .sv-carousel-control {
    position: absolute;
    top: 50%;
    z-index: 999;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 3rem;
    height: 3rem;
    border: 1px solid red;
    border-radius: 50%;
    outline: none;
}
.sv-carousel .sv-carousel-control .arrow-icon {
      display: inline-block;
      width: 1rem;
      height: 1rem;
      border-left: 2px solid red;
      border-bottom: 2px solid red;
}
.sv-carousel .sv-carousel-control.sv-carousel-control-prev {
      left: 1rem;
}
.sv-carousel .sv-carousel-control.sv-carousel-control-prev .arrow-icon {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
.sv-carousel .sv-carousel-control.sv-carousel-control-next {
      right: 1rem;
}
.sv-carousel .sv-carousel-control.sv-carousel-control-next .arrow-icon {
        -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
}



