/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-nodata-wrapper {
  position: fixed;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.sv-nodata-wrapper .sv-nodata-icon {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 8.5rem;
    height: 8.5rem;
    margin: 0 auto;
    border-radius: 50%;
    background: #e7e7e7;
}
.sv-nodata-wrapper .sv-nodata-icon.refreshing:active {
      background: #e7e7e7;
}
.sv-nodata-wrapper .sv-nodata-icon:active {
      background: #e1e1e1;
}
.sv-nodata-wrapper .sv-nodata-icon .icon.icon-no-data {
      color: #cfcfcf;
      font-size: 5rem;
}
.sv-nodata-wrapper .sv-nodata-icon .icon.icon-refresh {
      width: 3rem;
      height: 3rem;
      border: 0.4rem solid #ff4949;
      border-top-color: #fff;
      border-radius: 1.5rem;
      color: #ff7800;
      font-size: 5rem;
      -webkit-animation: rotate 2s infinite linear;
              animation: rotate 2s infinite linear;
}
@-webkit-keyframes rotate {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotate {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.sv-nodata-wrapper .sv-nodata-text {
    margin-top: 1.6rem;
    text-align: center;
    font-size: 1.4rem;
    color: #999;
}
.sv-nodata-wrapper .sv-nodata-text.refreshing {
      color: #ff4949;
}



