/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0rem 1rem;
  height: 5rem;
  line-height: 1;
  text-align: center;
  background: #fff;
  font-size: 1.3rem;
}
.sv-header.is-fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    border-bottom: 0.05rem solid #bfbfbf;
}
.sv-header .sv-header-left {
    -webkit-box-flex: .5;
        -ms-flex: .5;
            flex: .5;
    text-align: left;
}
.sv-header .sv-header-left .back-icon {
      font-size: 1.9rem;
      color: #fff;
}
.sv-header .sv-header-title {
    -webkit-box-flex: 3;
        -ms-flex: 3;
            flex: 3;
    font-size: 1.8rem;
    color: #fff;
}
.sv-header .sv-header-title .sv-input-wrapper {
      height: 3rem;
      border: 1px solid #999;
      border-radius: 1.5rem;
      font-size: 1.3rem;
}
.sv-header .sv-header-title .sv-input-wrapper .sv-input-icon {
        width: auto;
        line-height: 3rem;
        background: none;
        border-left: none;
        color: #aaa;
        font-size: 1.6rem;
}
.sv-header .sv-header-title .sv-input-wrapper .sv-input {
        height: 100%;
        padding-right: 1rem;
        background: transparent;
        outline: none;
        border: none;
        font-size: 1.3rem;
}
.sv-header .sv-header-right {
    -webkit-box-flex: .5;
        -ms-flex: .5;
            flex: .5;
}
.sv-header .sv-header-right .sv-button {
      padding: 0;
}
.sv-header.sv-header-light {
    background: #fff;
}
.sv-header.sv-header-light .sv-header-left .back-icon {
      color: #999;
}
.sv-header.sv-header-light .sv-header-title {
      color: #333;
}
.sv-header.sv-header-primary {
    background: #2d8cf0;
}
.sv-header.sv-header-success {
    background: #65ccaf;
}



