
@charset "UTF-8";
/*********************** reset ***********************/
html, body, div, span, h1, h2, h3, h4, h5, h6, p, img, ul, li, figure, figcaption {
  margin: 0;
  padding: 0;
}
html {
  /*
  移动端如何定义字体font-family
@ --------------------------------------中文字体的英文名称
@ 宋体      SimSun
@ 黑体      SimHei
@ 微信雅黑   Microsoft Yahei
@ 微软正黑体 Microsoft JhengHei
@ 新宋体    NSimSun
@ 新细明体  MingLiU
@ 细明体    MingLiU
@ 标楷体    DFKai-SB
@ 仿宋     FangSong
@ 楷体     KaiTi
@ 仿宋_GB2312  FangSong_GB2312
@ 楷体_GB2312  KaiTi_GB2312
@
@ 说明：中文字体多数使用宋体、雅黑，英文用Helvetica
   */
  font-family: 'Microsoft YaHei', 'SimSun', 'Tahoma', 'Helvetica';
  font-size: 10px;
  background: #fff;
  height: 100%;
  min-height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}
img {
  border: 0;
  vertical-align: middle;
}
ol, ul {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover, a:focus {
    outline: none;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: 100%;
}
button, input, textarea {
  margin: 0;
  font: inherit;
  color: inherit;
  outline: none;
}
* {
  box-sizing: border-box;
}
:before, :after {
  box-sizing: border-box;
}
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/*********************** sass function ***********************/
/* px to rem  */
/*********************** driver mobile UI style ***********************/
[v-cloak] {
  display: none !important;
}
body {
  width: 100%;
  height: 100%;
  min-height: 100%;
}

/*基本颜色*/
/*白色*/
/*灰色*/
/*蓝色*/
/*深蓝色*/
/*浅蓝色*/
/*绿色*/
/*黄色*/
/*红色*/
/*深红色*/
/*橙色*/
/*紫色*/
/* background color */
.bg-danger {
  background: #ff7800;
}
.bg-success {
  background: #65ccaf;
}
.bg-terrible {
  background: #999;
}

/* font color */
.font-danger {
  color: #ff7800;
}
.font-success {
  color: #65ccaf;
}
.font-terrible {
  color: #999;
}
.sv-toast {
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 0.8rem 1rem;
  border-radius: 0.5rem;
  background: rgba(0, 0, 0, 0.8);
  color: #f2f2f2;
  font-size: 1.6rem;
  -webkit-animation: fade-out .5s;
          animation: fade-out .5s;
}
@-webkit-keyframes fade-in {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fade-in {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@-webkit-keyframes fade-out {
to {
    opacity: 0;
}
from {
    opacity: 1;
}
}
@keyframes fade-out {
to {
    opacity: 0;
}
from {
    opacity: 1;
}
}
.sv-toast.is-active {
    -webkit-animation: fade-in .5s;
            animation: fade-in .5s;
}
.sv-toast.sv-toast-top {
    top: 5rem;
}
.sv-toast.sv-toast-middle {
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.sv-toast.sv-toast-bottom {
    bottom: 5rem;
}
.sv-toast .sv-toast-icon {
    margin-bottom: 1rem;
}



